import React, { Component } from 'react';
import { Container, Col } from 'reactstrap';
import ReactMarkdown from 'react-markdown';
import Fade from 'react-reveal/Fade';
import mdproj from './mdproj.md';
import toc from 'markdown-toc';

import CodeBlock from './CodeBlock';
import contents from './ContentsTable'

function heading(props) {
  let hdng = [React.createElement('div', {class: 'indented-block'}, React.createElement(`h${props.level}`,
                             {id: props.children[0].props.value.replace(/\s/g, '-')},
                                                                                          props.children))];
  if (props.level === 2){
    hdng.unshift( <hr style={{background: 'white', backgroundClip: 'initial'}}/> );
  }
  return hdng;
}

class Projects extends Component {
    constructor(props) {
        super(props);
        this.state = {md : ''}
    }
    componentDidMount() {

  fetch(mdproj).then((response) => response.text()).then((text) => {
    this.setState({ md: text })
    })
    }
    render(){
       return  (<Fade bottom  cascade text>
                  <ReactMarkdown escapeHtml={false} renderers={{code: CodeBlock, root: contents, heading: heading}} source={this.state.md}/>
                  <hr className='vibrant' />
            This website was built with React and Webpack, it is a simple static site on gitHub pages. If you experience any problems viewing and enjoying my site consider creating an Issue on my github page.
        </Fade>);
    }

}

export default Projects;
